import React, {Component} from 'react';

import SoundBank from '../components/soundBank';
import Sounds from '../sounds/sounds';
import ReactHowler from 'react-howler';

class SoundBankContainer extends Component{
  constructor(props){
    super(props)
    let sounds = Sounds();
    console.log(sounds);
    this.state = {
      sounds: sounds.tree,
      head: sounds.head,
      step: null,
      playing: false,
      filePath: ""
    }

    this.stepInto = this.stepInto.bind(this);
    this.stepUp = this.stepUp.bind(this);
    this.soundClicked = this.soundClicked.bind(this);
    this.stopSound = this.stopSound.bind(this);
    this.onPlayEnd = this.onPlayEnd.bind(this);
  }

  stepInto(step){
    this.setState({
      step:step
    });
  }

  stepUp(step){
    if(typeof step === 'undefined'){return;}
    this.setState({
      step:step.parent
    });
  }

  soundClicked(item){
    if(this.state.filePath === item.path){
      return this.stopSound();
    }
    this.setState({
      filePath:item.path,
      playing: true
    });
  }

  stopSound(){
    this.setState({
      filePath:"",
      playing:false
    })
  }

  onPlayEnd(){
    this.setState({
      playing:false
    });
  }

  render(){
    const {sounds, head, step, playing, filePath} = this.state;
    return (
      <div>
        <SoundBank
          sounds={sounds}
          head={head}
          step={step}
          filePath={filePath}
          soundClicked={this.soundClicked}
          stopSound={this.stopSound}
          stepInto={(step)=>this.stepInto(step)}
          stepUp={step=>this.stepUp(step)}/>
        <ReactHowler
          src={"./sounds/"+filePath}
          playing={playing}
          loop={false}
          html5={false}
          preload={false}
          onEnd={this.onPlayEnd}/>
      </div>
    )
  }
}

export default SoundBankContainer;



